@import url('https://fonts.googleapis.com/css2?family=Lato:wght@700;800&display=swap');

*, *::before, *::after {
  box-sizing: border-box;
}

// colors
$color-pink: #FF70D9;
$color-yellow: #EBFF00;
$color-blue: #304571;
$color-black: #000000;
$color-white: #FFFFFF;

// breakpoints
$desktop: 1248px;
$tablet: 768px;
$mobile: 360px;

// mixins
@mixin breakpoint($size) {
  @if $size == desktop {
    @media (max-width: $desktop) { @content; }
  }
  @else if $size == tablet {
    @media (max-width: $tablet) { @content; }
  }
  @else if $size == mobile {
    @media (max-width: $mobile) { @content; }
  }
}

@mixin font-size($font-size, $line-height, $color) {
  font-size: $font-size;
  line-height: $line-height;
  color: $color;
}

@mixin button-style {
  width: 191px;
  height: 54px;

  background-color: transparent;
  border: 1px solid $color-yellow;
  border-radius: 100px;

  &:hover {
    opacity: 60%;
    transition: all ease-in-out .1s;
  }
}

// styles
body {
  font-family: 'Lato', sans-serif;
  font-weight: 800;
  overflow-x: hidden;
}

.bold-text {
  font-weight: 700 !important;
}

.app-wrapper {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 1.4rem 3.3rem 1.4rem 5rem;

    background-color: $color-black;

    @include breakpoint(tablet) {
      padding: 1.75rem 1.7rem 1.75rem 1.9rem;
    }

    @include breakpoint(mobile) {
      padding: .75rem 1.25rem;
    }

    .logos-container {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 6px;

      .logo-first {
        width: 35px;
        height: 35px;

        @include breakpoint(tablet) {
          width: 24px;
          height: 24px;
        }
      }

      .logo-second {
        width: 63px;
        height: 25px;

        @include breakpoint(tablet) {
          width: 43px;
          height: 17px;
        }
      }
    }

    .navigation {
      display: flex;
      align-items: center;
      gap: 42px;

      @include breakpoint(tablet) {
        gap: 50px;
      }

      .navigation__button {
        all: unset;
        cursor: pointer;
        color: $color-white;
        overflow: hidden;

        &:hover {
          background: linear-gradient(90deg, #EBFF00 0%, #FF70D9 100%);
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;

          .basket {
            display: none;
          }

          .basket_hover {
            display: flex;
          }
        }

        @include breakpoint(mobile) {
          display: none;
        }

        .basket {
          display: flex;
        }

        .basket_hover {
          display: none;
        }
      }

      .menu-button {
        display: flex;
        justify-content: center;

        &:hover {
          .burger-menu_hover {
            display: flex;

            width: 32px;
            height: 38px;
          }

          .burger-menu {
            display: none;
          }
        }

        .burger-menu {
          display: none;
  
          @include breakpoint(mobile) {
            display: flex;
          }
        }

        .burger-menu_hover {
          display: none;
        }
      }

      .menu-toggle {
        display: none;
      }
      
      .menu {
        display: none;
      
        &.active {
          display: flex;
        }
      }
      
      .menu-toggle:checked + .menu-button + .menu {
        position: absolute;
        top: 60px;
        right: 0px;

        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        gap: 5px;

        width: 75px;
        height: 100px;

        background-color: $color-black;
        color: $color-white;
        z-index: 1;
      }
    }
  }

  .banner-section {
    height: 490px;

    background-image: 
    linear-gradient(to left, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, .5) 100%),
    url('./assets/images/information-backgr.png');
    background-size: cover;
    background-position: center;

    @include breakpoint(tablet) {
      height: 420px; 
      overflow: hidden;
    }

    @include breakpoint(mobile) {
      background-image: 
      linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, .7) 100%),
      url('./assets/images/information-backgr.png');
    }

    .earth-image {
      position: relative;
      top: 10%;
      left: 67%;

      @include breakpoint(tablet) {
        top: 6%;
      }

      @include breakpoint(mobile) {
        top: -30%;
        left: 5%;
      }
    }

    .info {
      position: relative;
      bottom: 67%;
      left: 6%;

      display: flex;
      flex-direction: column;
      gap: 10px;

      width: 693px;

      @include breakpoint(tablet) {
        gap: 20px;
        width: 400px;
      }

      @include breakpoint(mobile) {
        align-items: center;
        gap: 17px;

        width: 290px;

        bottom: 44%;
        left: 8%;
      }

      .info__title {
        @include font-size(72px, 72px, $color-white);

        @include breakpoint(tablet) {
          @include font-size(48px, 48px, $color-white);
        }

        @include breakpoint(mobile) {
          @include font-size(36px, 36px, $color-white);
        }
      }

      .info__title_bright {
        color: $color-pink;
      }

      .info__description {
        @include font-size(32px, 72px, $color-white);

        @include breakpoint(tablet) {
          @include font-size(22px, 26px, $color-white);
        }

        @include breakpoint(mobile) {
          font-size: 16px;
        }
      }

      .info__description_bright {
        color: $color-yellow;
      }

      .info__button {
        width: 191px;
        height: 54px;

        background-color: $color-yellow;
        border-radius: 100px;
        border: none;

        @include breakpoint(mobile) {
          width: 161px;
          height: 33px;
        }

        &:hover {
          opacity: 60%;
          transition: all ease-in-out .1s;
        }
      }

      .info__button__title {
        color: $color-black;
        font-size: 22px;

        @include breakpoint(mobile) {
          font-size: 16px;
        }
      }
    }
  }

  .offers-section {
    display: flex;
    flex-direction: column;
    gap: 40px;

    padding: 3.6rem 5rem 3.9rem 5rem;

    background-color: $color-blue;

    @include breakpoint(tablet) {
      gap: 30px;
      padding: 1.2rem;
    }

    .section-title {
      font-size: 32px;
      color: $color-white;

      @include breakpoint(tablet) {
        font-size: 24px;
      }

      @include breakpoint(mobile) {
        font-size: 18px;
      }
    }

    .offers-container {
      display: flex;
      flex-wrap: wrap;
      gap: 40px;
      
      @include breakpoint(tablet) {
        gap: 29px;
      }
    }

    .offer {
      display: flex;
      flex-direction: column;
      gap: 22px;

      width: 100%;
      padding: 2.2rem;

      background-image: 
      linear-gradient(to left, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, .6) 100%),
      url('./assets/images/first-offer.png');
      background-position: 90% 47%;
      border-radius: 10px;

      @include breakpoint(tablet) {
        display: flex;

        padding: 4.5rem 6.25rem 1.7rem 1.9rem;
        width: 350px;
        height: 339px;

        background-position: 90% 47%;
        background-size: 100%;
      }

      @include breakpoint(mobile) {
        width: 320px;
        height: 350px;

        background-size: 100%;
        background-repeat: no-repeat;
        background-size: cover;
      }

      .offer__title {
        width: 600px;

        @include font-size(74px, 74px, $color-white);

        @include breakpoint(tablet) {
          width: 195px;

          @include font-size(40px, 40px, $color-white);
        }
      }

      .offer__description {
        font-size: 26px;
        color: $color-white;

        @include breakpoint(tablet) {
          font-size: 16px;
        }
      }

      .offer__button {
        @include button-style;

        .offer__button__title {
          font-size: 22px;
          color: $color-yellow;

          @include breakpoint(tablet) {
            font-size: 16px;
          }
        }
      

        @include breakpoint(tablet) {
          width: 161px;
          height: 33px;
        }
      }
    }

    .second {
      background-image: 
      linear-gradient(to left, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, .6) 100%),
      url('./assets/images/fourth-offer.png');
      background-position: 100% 52%;

      @include breakpoint(tablet) {
        padding: 5.5rem 1rem 1rem 2rem;
        background-position: 70% 100%;
      }
    }

      .small-offer {
        display: flex;
        flex-direction: column;
        gap: 22px;

        width: 524px;
        padding: 3rem 2.2rem;
  
        background-image: 
        linear-gradient(to left, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, .6) 100%),
        url('./assets/images/second-offer.png');
        background-position: 0% 45%;
        background-size: 100%;
        border-radius: 10px;

        @include breakpoint(tablet) {
          width: 350px;
          height: 339px;
          padding: 5.5rem 6.25rem 1.7rem 1.7rem;

          background-position: 90% 7%;
          background-size: 100%;
        }

        @include breakpoint(mobile) {
          width: 320px;
          height: 350px;

          background-size: 100%;
          background-repeat: no-repeat;
          background-size: cover;
        }
  
        .small-offer__title {
          width: 250px;
  
          font-size: 46px;
          line-height: 46px;
          color: $color-white;

          @include breakpoint(tablet) {
            width: 240px;
  
            font-size: 40px;
            line-height: 40px;
          }
        }
  
        .small-offer__description {
          font-size: 24px;
          color: $color-white;

          @include breakpoint(tablet) {
            font-size: 16px;
          }
        }
  
        .small-offer__button {
          @include button-style;

          @include breakpoint(tablet) {
            width: 161px;
            height: 33px;
          }
  
          .small-offer__button__title {
            font-size: 22px;
            color: $color-yellow;

            @include breakpoint(tablet) {
              font-size: 16px;
            }
          }
        }
      }

      .small-second {
        background-image: 
        linear-gradient(to left, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, .6) 100%),
        url('./assets/images/third-offer.png') !important;
        background-position: 30% 55%;
        background-size: 100%;
        background-repeat: no-repeat;

        @include breakpoint(tablet) {
          padding: 5.5rem 1rem 1rem 2rem;
          background-position: 50% 25%;
        }

        @include breakpoint(mobile) {
          background-repeat: no-repeat;
          background-size: cover;
        }
      }
    
  }

  .read-container {
    margin-top: 22px;

    display: flex;
    flex-direction: column;
    gap: 17px;

    .read__title {
      font-size: 32px;
      color: $color-white;

      @include breakpoint(tablet) {
        font-size: 24px;
      }

      @include breakpoint(mobile) {
        font-size: 18px;
      }
    }

    .read-more-toggle {
      display: none;
    }

    .read__button {
      cursor: pointer;
      color: $color-yellow;
      font-size: 20px;

      display: flex;
      max-width: 150px;
      margin-left: 0;

      &:hover {
        opacity: 60%;
      }

      @include breakpoint(tablet) {
        max-width: 120px;

        font-size: 16px;
      }

      @include breakpoint(mobile) {
        font-size: 14px;
      }

      .read__button_more {
        opacity: 1;
      }

      .read__button_hide {
        position: absolute;

        opacity: 0;
      }
    }

    .read__full-description {
      display: none;
    
      @include breakpoint(mobile) {
        font-size: 14px;
        color: $color-white;
        opacity: 60%;
        display: block;
        max-height: 0;
        overflow: hidden;
        transition: max-height 0.3s ease-in-out;
      }
    }

    .read__description {
      opacity: 60%;
      @include font-size(20px, 24px, $color-white);

      @include breakpoint(tablet) {
        @include font-size(16px, 19px, $color-white);
      }

      @include breakpoint(mobile) {
        @include font-size(14px, 17px, $color-white);
      }
    }

    .read-more-toggle:checked + .read__button + .read__full-description {
      max-height: 100px;
    }

    .read-more-toggle:checked + .read__button ~ .read__description .read__full-description {
      display: inline;
      opacity: 1;
    }

    .read-more-toggle:checked + .read__button .read__button_more {
      @include breakpoint(desktop) {
        opacity: 1;
      }

      @include breakpoint(tablet) {
        opacity: 1;
      }

      @include breakpoint(mobile) {
        opacity: 0;
      }
    }
    
    .read-more-toggle:checked + .read__button .read__button_hide {
      @include breakpoint(mobile) {
        opacity: 1;
      }
    }
  }

  .footer {
    display: flex;
    align-items: center;
    flex-direction: column;

    height: 112px;
    padding-bottom: 1.2rem;

    background-color: $color-black;

    .footer__img {
      width: 82px;
      height: 108px;
    }

    .footer__description {
      font-size: 18px;
      color: $color-yellow;
    }
  }
}